import { Fragment, useEffect, useRef } from "react";
import "./tr-digital-dentistry.style.scss";
import { ReactComponent as ArrowUp } from "../../../assets/check-1.svg";
const TRDigitalDentistry = () => {
  const digitalDentRef = useRef();
  const showDescription = (e) => {
    let num = 0;
    digitalDentRef.current.childNodes.forEach((nod) => {
      if (nod.childNodes[1].classList.contains("tr-digitaldent")) {
        num += 1;
      }
      nod.childNodes[0].childNodes[1].classList.remove("tr-digitaldent");
    });
    if (e.currentTarget.childNodes[1].classList.contains("tr-digitaldent")) {
      if (num > 1) {
        e.currentTarget.childNodes[1].classList.remove("tr-digitaldent");
        e.currentTarget.childNodes[1].classList.add("tr-digital-hidden");
        e.currentTarget.childNodes[0].childNodes[1].classList.remove(
          "tr-digit-svg"
        );
      }
    } else {
      e.currentTarget.childNodes[0].childNodes[1].classList.add("tr-digit-svg");
      e.currentTarget.childNodes[1].classList.add("tr-digitaldent");
      e.currentTarget.childNodes[1].classList.remove("tr-digital-hidden");
    }
  };
  useEffect(() => {
    document.title = "Digital Dentistry - BilimDent";
    window.scrollTo({ behavior: "smooth" }, 0, 0);
  });
  return (
    <Fragment>
      <div className="tr-digital-dentistry">
        <div className="tr-digital-dentistry-wrap">
          <div className="tr-digital-dentistry-header">
            <div className="tr-digital-dentistry-heading">
              DGTAL<span> D </span>HEKML
            </div>
          </div>
          <div className="tr-digital-dentistry-container">
            <div className="tr-digital-dentistry-cont" ref={digitalDentRef}>
              <div
                className="tr-digital-dentistry-desc"
                onClick={showDescription}
              >
                <div className="tr-digit-header">
                  <span className="tr-digit-head">
                    DJTAL D HEKML NEDR?
                  </span>
                  <span className="tr-digit-svg-down">
                    <ArrowUp />
                  </span>
                </div>
                <div className="tr-digital-dentistry-w tr-digitaldent tr-digitalDent-back-1">
                  <p className="tr-digital-dentistry-description">
                    Dijital di hekimlii, tehis, planlama ve tedavi srasnda
                    tamamen veya ksmen bilgisayar destekli cihazlarn,
                    yntemlerin ve teknolojinin kullanlmas anlamna gelir.
                  </p>
                  <p className="tr-digital-dentistry-description">
                    Dijital di hekimlii yntemleri insan hatasn azaltp,
                    retim hassasiyetini artrmak ve tedavi srelerini ksaltmak
                    amacyla gelitirilmi yntemlerdir.
                  </p>
                  <p className="tr-digital-dentistry-description">
                    Basit bir dolgudan komplike implant tedavilerine, estetik
                    planlamadan gl tasarmna, ortodontiden porselen
                    kaplamalara kadar birok uygulama artk dijital teknolojiler
                    ve cihazlar kullanlarak yaplabilmekte.
                  </p>
                  <p className="tr-digital-dentistry-description">
                    Dijital yntemlerin hz ve retim hassasiyeti gibi
                    avantajlarnn yannda ok nemli bir avantaj da hastaya
                    retilen dolgu, kaplama, kpr, ortodonti plaklar, gece
                    plaklar ve benzeri tm uygulamalarn retim datalar veri
                    tabanlarnda saklanabilmekte ve bir gn herhangi bir nedenle
                    tekrar retilmesi gerektiinde hastadan tekrar l almadan
                    ayn data ile retim yaplabiliyor olmasdr.
                  </p>
                  <p className="tr-digital-dentistry-description">
                    Bir baka avantaj da hastann mevcut az durumu taranp
                    kaydedilebilmekte ve ileride yaplan yeni taramalar ile
                    karlatrlabiliyor olmasdr. Bu sayede anmalar, yer
                    deitirmeler ve benzeri deiikliklerin belirlenebiliyor
                    olmasdr.
                  </p>
                </div>
              </div>
              <div
                className="tr-digital-dentistry-desc"
                onClick={showDescription}
              >
                <div className="tr-digit-header">
                  <span className="tr-digit-head">
                    DJTAL CERRAH REHBER (SURGCAL GUDED IMPLANTS)
                  </span>
                  <span className="tr-digit-svg-down">
                    <ArrowUp />
                  </span>
                </div>
                <div className="tr-digital-dentistry-w tr-digital-hidden tr-digitalDent-back-2">
                  <p className="tr-digital-dentistry-description">
                    mplant cerrahisi srasnda implantlarn ene kemiine en
                    doru ada ve en doru konumda yerletirilmesini salayarak
                    implant cerrahisinin baarsn arttran dijital cerrahi
                    rehber, di eti kaldrlma gereksinimini ortadan kaldrarak
                    kanama miktarn azaltr.
                  </p>
                  <p className="tr-digital-dentistry-description">
                    Cerrahi travmalarn azaltlmasn salayan dijital cerrahi
                    rehber uygulamas ncesi 3 boyutlu tomografi ekilerek
                    hastalarn ene kemii grntleri detayl bir ekilde elde
                    edilir. Eksilen dilerin bulunduu ksmlarn analizi
                    gerekletirilir ve eksik dii tamamlamak iin
                    yerletirilecek olan implantn boyutu, as ve konumu
                    dikkatli bir ekilde ayarlanr.
                  </p>
                  {/* <picture className="tr-digital-dentistry-image">
                    <img
                      width="600px"
                      height="420px"
                      src={require("../../../assets/digital-dentistry/surgical-guid.jpg")}
                    />
                  </picture> */}
                </div>
              </div>
              <div
                className="tr-digital-dentistry-desc"
                onClick={showDescription}
              >
                <div className="tr-digit-header">
                  <span className="tr-digit-head">LAZER D HEKML</span>
                  <span className="tr-digit-svg-down">
                    <ArrowUp />
                  </span>
                </div>
                <div className="tr-digital-dentistry-w tr-digital-hidden tr-digitalDent-back-3">
                  <p className="tr-digital-dentistry-description">
                    Lazer iyonize olmayan bir ktr ve dokuda birikim yapmaz.
                    Bu sebeple x n gibi zarar verecek bir zellii de
                    yoktur. Uygun lazer dalga boyu kullanlarak doru dozlarla
                    kullanm nemlidir. Hamileler, ocuklar ve kalp hastalar
                    da dahil olmak zere herkeste kullanlabilir. Lazer
                    kullanlan blgede dezenfeksiyon ve sterilizasyon
                    salanmaktadr.
                  </p>
                  <p className="tr-digital-dentistry-description">
                    Gc ayarlanabilen lazer nnn rk dokuyu seici
                    davranmas az madde kaybyla tedavinin tamamlanmasn
                    salar. (Diten sadece rk blgeyi kaldrr.)
                  </p>
                  <p className="tr-digital-dentistry-description">
                    Lazer ile yaplan cerrahi uygulamalarda, ilem esnasnda
                    neredeyse hi kanama olmamaktadr. Her cerrahi uygulama
                    sonrasnda ortaya kabilecek komplikasyon, ime ve
                    rahatszlk olaslklar azalmakta ve daha hzl bir
                    iyileme salanmaktadr.
                  </p>
                </div>
              </div>
              <div
                className="tr-digital-dentistry-desc"
                onClick={showDescription}
              >
                <div className="tr-digit-header">
                  <span className="tr-digit-head">DJTAL 3D GRNTLEME</span>
                  <span className="tr-digit-svg-down">
                    <ArrowUp />
                  </span>
                </div>
                <div className="tr-digital-dentistry-w tr-digital-hidden tr-digitalDent-back-4">
                  <p className="tr-digital-dentistry-description">
                    Gnmzde  boyutlu gelimi tomografi rntgenleri ile
                    yaklak 30 saniye gibi bir srede dilerin ve nemli
                    anatomik yaplarn haritasn karabilmekteyiz. Bu sayede
                    gerek implant uygulamalar gerek estetik di tedavisi
                    uygulamalar zenle planlanarak hi risk almadan kusursuz
                    yaplabilmektedir.
                  </p>
                </div>
              </div>
              <div
                className="tr-digital-dentistry-desc"
                onClick={showDescription}
              >
                <div className="tr-digit-header">
                  <span className="tr-digit-head">DJTAL GL TASARIMI</span>
                  <span className="tr-digit-svg-down">
                    <ArrowUp />
                  </span>
                </div>
                <div className="tr-digital-dentistry-w tr-digital-hidden tr-digitalDent-back-5">
                  <p className="tr-digital-dentistry-description">
                    Di hekimleri dijital yazlmlar sayesinde hastann di
                    yaps, di etleri, dudaklar ve yz arasndaki en doru
                    uyumu yakalayabiliyor. Bylece kiiye doal ve estetik bir
                    gl oluturulabiliyor. Dijital gl tasarmnda hasta
                    odakl bir tedavi yaplr. Her kiiye yakan gl ekli
                    farkldr. Bu yzden di hekimleri kiiye en uygun gl
                    tasarlar.
                  </p>
                </div>
              </div>
              <div
                className="tr-digital-dentistry-desc"
                onClick={showDescription}
              >
                <div className="tr-digit-header">
                  <span className="tr-digit-head">DJTAL L VE TARAMA</span>
                  <span className="tr-digit-svg-down">
                    <ArrowUp />
                  </span>
                </div>
                <div className="tr-digital-dentistry-w tr-digital-hidden tr-digitalDent-back-6">
                  <p className="tr-digital-dentistry-description">
                    Bazen klasik ve eskiden sre gelen geleneksel sistemle l
                    alnmas srasnda rme refleksi gereklemektedir. Bu
                    durumda net l alnamama gibi problemler yaanabilir.
                    Dijital l ileminde bu tip sorunlar minimum seviyede
                    yaanabilmektedir. Alnan grntler sanal ortama aktarlp
                    bilgisayar modellemesi yaplarak net uygulamalar yapmak
                    mmkn olur. Dijital l sayesinde l neredeyse kusursuz
                    olmakta ve hastada oluabilicek rahatszlklar olduka
                    azaltlmaktadr.
                  </p>
                  <p className="tr-digital-dentistry-description">
                    Dijital di hekimliinde optik bir kamera ile diler taranr
                    ve 3 boyutlu olarak bilgisayar ortamna aktarlarak 3
                    boyutlu tarama yntemi ile gerekletirilir.
                  </p>
                </div>
              </div>
              <div
                className="tr-digital-dentistry-desc"
                onClick={showDescription}
              >
                <div className="tr-digit-header">
                  <span className="tr-digit-head">NLAP SSTEM</span>
                  <span className="tr-digit-svg-down">
                    <ArrowUp />
                  </span>
                </div>
                <div className="tr-digital-dentistry-w tr-digital-hidden tr-digitalDent-back-7">
                  <p className="tr-digital-dentistry-description">
                    Yeni inLab CAD Yazlm, di laboratuvarnda CAD / CAM
                    sistemlerinin gereksinimlerine artk daha da uyumlu. Ayr
                    bir CAD bileeni olarak, yazlm tarama ve retim
                    nitesinden bamszdr. inLab CAD Yazlm, optimize
                    tasarm sreleri ve kullanc dostu arayz ile ihtiyaca
                    dayal endikasyonlar kapsar.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};
export default TRDigitalDentistry;
