import { Fragment, useEffect, useRef } from "react";
import "./tr-footer.style.scss";
import { Link, useLocation } from "react-router-dom";
const TRFooter = () => {
  const footerRef = useRef();
  const loc = useLocation();
  useEffect(() => {
    footerRef.current?.childNodes.forEach((foot) => {
      if (foot.pathname === loc.pathname) {
        foot.style = "color: #ba9e66";
      } else {
        foot.style = "color: #fff";
      }
    });
  }, [loc]);
  return (
    <Fragment>
      <footer className="tr-footer">
        <div className="tr-footer-container">
          {/* <div className="tr-footer-wrap">
            <p className="tr-footer-heading">Purpose And Mission</p>
            <div className="tr-footer-description-1">
              Our main aim is to perform Oral Diagnosis, Prosthetic Dentistry,
              Pedodontics, Endodontics, Orthodontics, Periodontology, Oral and
              Maxillofacial Surgery and Restorative Dental treatments by using
              the most advanced technologies in the clinics of our institution.
            </div>
          </div> */}
          <div className="tr-footer-wrap">
            <p className="tr-footer-heading">Adres</p>
            <div className="tr-footer-description-3">
              Tahlpazar Mahallesi, Kazm zalp Cd. No 84 D:Floor 9, 07040
              Muratpasa/Antalya
            </div>
          </div>
          <div className="tr-footer-wrap">
            <p className="tr-footer-heading">letiim</p>
            <div className="tr-footer-desc">
              <div className="tr-footer-desc-info">
                <h4>Telefon</h4>
                <div>+90 242 245 00 11</div>
              </div>
              <div className="tr-footer-desc-info">
                <h4>Eposta</h4>
                <div>bilimdent@antalya.edu.tr</div>
              </div>
              <div className="tr-footer-desc-info">
                <h4>Telefon</h4>
                <div>+90 544 245 00 91</div>
              </div>
            </div>
          </div>
        </div>
        <div className="tr-copyrights">
          Copyright 2022 bilimdent.com | Tm Haklar Sakldr
        </div>
        <div className="tr-footer-foot">
          <div className="tr-footer-foot-container">
            <div>
              <img
                height="64px"
                width="78px"
                src={require("../../../assets/renkli-1.png")}
              />
            </div>
            <div>
              <img
                height="64px"
                width="78px"
                src={require("../../../assets/renkli-2.png")}
              />
            </div>
            <div className="tr-footer-by">BY</div>
            <div className="tr-iimm">
              <a href="https://antalya.edu.tr/en" target="_">
                <img
                  height="51px"
                  width="240px"
                  src={require("../../../assets/uni-logo.png")}
                />
              </a>
            </div>
          </div>
        </div>
      </footer>
    </Fragment>
  );
};
export default TRFooter;
